export default {
	data() {
		return {
			list: [],
			Newlist: [],
			refreshing: false,
			fetchPageNum: 1,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			newsList: [],
			tabIndex: 0,
			grids: [{
				title: '最新',
				address: 0,
				Number: 0,
				xinimg: 'http://placehold.it/150x150'
			}],
		}
	},
	onLoad: function() {
		this.getImgData();
		this.getGrids();
	},
	methods: {
		getImgData() {
			uni.request({
				url: this.$serverUrl + '/api/picture/posts.php?page=' + (this.refreshing ? 1 : this.fetchPageNum) +
					'&per_page=5',
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log("失败!");
					} else {
						this.list = ret.data;
					}
				},
				fail: (ret) => {
					uni.onNetworkStatusChange(function(res) {
						console.log(res.isConnected);
						console.log(res.networkType);
					});
				},
				complete: (ret) => {
					this.loading = false;
				}
			});
		},
		
		getGrids() {
			uni.request({
				url: this.$liveUrl + '/json.txt',
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log("失败!");
					} else {
						this.grids = ret.data.pingtai;
					}
				},
				fail: (ret) => {
					uni.onNetworkStatusChange(function(res) {
						console.log(res.isConnected);
						console.log(res.networkType);
					});
				},
				complete: (ret) => {
					this.loading = false;
				}
			});
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},

		tapIcon(e) {
			uni.navigateTo({
				url: '../channel/channel?address='+ e.address,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		openLeftList() {
			uni.navigateTo({
				url: '../leftlist/leftlist',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		openMore() {
			uni.navigateTo({
				url: '../moreList/moreList',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		openPicture() {
			uni.navigateTo({
				url: '../picture/picture',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		showDefImg(evt)
		{	
			
		}
	}
}
